import React from 'react';
import {Modal,message} from 'antd'; 
import { observer, inject } from 'mobx-react';
import classnames from 'classnames';
import CommonKeyboardNum from '../common-keyboard-num';//键盘
import MyScroll from 'react-custom-scrollbars';//滚动条
import Accredit from 'components/accredit-popup';//二次授权
import './zj-estimate-popup.less';  
message.config({
	top: 300 
});

//增加/减少/报损
@inject('selloutStore') @observer
class ZjEstimatePopup extends React.Component {
  constructor(props, context) {
    super(props, context);  
    this.state={
        myValue:'',//数字文本框
        Accredit:'',//授权
        recordValue:''//其他原因文本 
      }
  }   
 

 //取消事件
 handleCancel=()=>{
   if(this.props.updateestime){
     this.props.updateestime();
   }
 }

 //确定事件
 handleOk=()=>{
    //  this.setState({Accredit:<Accredit handleCancelpopup={()=>{
    //    this.setState({Accredit:''})
    //  }}></Accredit>})   
    if(this.state.myValue === "" ){
        message.warn("请输入数量");  
        return;
    }if(this.props.selloutStore.reasontext.reasonName === "" && this.state.recordValue === ""){
       message.warn("请选择原因"); 
       return; 
    }else if(this.state.recordValue !== "" && this.state.recordValue.length >100){
      message.warn("输入的原因不能超过100个字"); 
       return; 
    }else{
        let quantity = this.state.myValue;//变动数量
        let textareaReason =  this.state.recordValue;//变动原因textarea 
        let changeReason = this.props.selloutStore.reasontext.reasonName === "" ? textareaReason : this.props.selloutStore.reasontext.reasonName; 
        this.props.selloutStore.determinebotton(quantity,changeReason); 
        if(this.props.okbutton){
          this.props.okbutton();
        } 
      
    }

   

 }
 //获取变动原因
 reachValue=(e)=>{ 
      var value = e.target.value;
      this.setState({recordValue:value}) 
  }

  //判断是否已经选了原因
  judge=(e)=>{ 
    let selloutStore=this.props.selloutStore;
    let reasonlist =selloutStore.reasonlist; 
    reasonlist.map((reason) => {
      reason.selected = false;
      return reason;
    });
    selloutStore.reasontext.reasonName = "";//清空勾选过的文本
      
  }
  render() {
    let selloutStore=this.props.selloutStore;
    let reasonlist =selloutStore.reasonlist; 
    return ( 
       <div>
          <Modal title={selloutStore.addorreducename}
              visible={true}
              closable={false}
              maskClosable={false}
              onCancel={this.handleCancel}
              onOk={this.handleOk}
              width={840} wrapClassName="zj-estimate-popup-modal"
            >
           <div className="zj-estimate-title">{selloutStore.afterobj.productName}</div>
           <div className="zj-estimate-left">
              <div className="zj-estimate-tupe">
                <span>{selloutStore.afterobj.optionName}</span>
                <input type="text" value={this.state.myValue}/>
              </div>
              <div className="zj-estimate-reason">
                <p>原因</p>
                <div className="div-reason">
                  <MyScroll > 
                    {(() =>{
                      if(reasonlist.length) {
                          return reasonlist.map((reason) => {
                          return <div key={reason.DictionaryID}  className={classnames({'each-reason': true, 'each-checked': reason.selected})}  
                                    onClick={()=>{ 
                                      selloutStore.selectreason(reason.DictionaryID);  //选中
                                      selloutStore.getreasontext(reason.DictionaryID,reason.DictionaryName);  //获取文本 
                                    }}
                                  > 
                                    {reason.selected && <i className="iconfont icon-icon_checkbox_sel"></i> }
                                    <span>{reason.DictionaryName}</span> 
                                </div>
                          });
                      }
                    })()}  
                  </MyScroll>
                </div>
              </div>
              <div className="zj-estimate-otherreason">
                <p>其他原因</p>
                <textarea onKeyUp={this.reachValue} onClick={this.judge}></textarea>
              </div>
           </div>
           <div className="zj-estimate-right">
            <div className="zj-right-keyboard">
              <CommonKeyboardNum width={382} height={380} Whetherpoint={selloutStore.afterobj.needWeigh ? true : false} getResult={(value)=>{
                            this.setState({myValue:value})
              }}></CommonKeyboardNum>
            </div>
           </div>
          </Modal>
          {this.state.Accredit}
       </div>
    );
  }
}



export default ZjEstimatePopup;



