/**
* @author shining
* @description 商品分类页面
* @date 2017-05-19
**/
import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import Scrollbars from 'react-custom-scrollbars';
import { Tabs, Modal  } from 'antd';
import classnames from 'classnames';

import './commodity-ification-popup.less';

const TabPane = Tabs.TabPane;

@inject('selloutStore') @observer
class CommodityIficationPopup extends Component {
	constructor(props,context){
		super(props,context);
		this.state = { 
			tabPosition: 'left',
            onelevelkey:'0',//一级分类的下标
            onelevelid:'',//一级分类id
            onelevelname:'',//一级分类name
            twolevelid:'',//二级分类id
            twolevelname:'',//二级分类name
            changeclick:0
		}
	}

	//点击取消按钮
	handleCancel=()=>{  
		if(this.props.closebutton){
			this.props.closebutton();
		} 
	}

    //点击确定按钮
    handleOk=()=>{ 
        
       let selloutStore = this.props.selloutStore;
       if(this.state.changeclick === 1){
             let lastid=this.state.onelevelid === "" ? this.state.twolevelid:this.state.onelevelid;
            let lastname=this.state.onelevelname === "" ? this.state.twolevelname:this.state.onelevelname;
            let lastkey=this.state.onelevelkey;
             if(this.state.twolevelname === ""){
                 selloutStore.saveshop="";
            }
            selloutStore.savelastcombination(lastid,lastname,lastkey);
       }else{ 
           selloutStore.savelastcombination(
               selloutStore.lastcombination.lastid,
               selloutStore.lastcombination.lastname,
               selloutStore.lastcombination.lastkey
            );
       }
     



        if(this.props.okbutton) this.props.okbutton();
    }
 
	
	render() {
	   let selloutStore = this.props.selloutStore;
      let commodityclassif = selloutStore.commodityclassifyList; 
 
		return (
			 <Modal title="商品分类" 
				visible={true} 
				maskClosable={false} 
				okText='确定' cancelText='取消'
				width={840} 
				wrapClassName="choose-class-popup-modal"
				onCancel={this.handleCancel}
				onOk={this.handleOk}
			 > 
				<Tabs defaultActiveKey={String(selloutStore.lastcombination.lastkey)} className="message-left" tabPosition={this.state.tabPosition}>
				 

                     {(() =>{
                    if(commodityclassif.length) {
                        return commodityclassif.map((commodity,i) => {   
                            return  <TabPane key={i} tab={ <div  onClick={()=>{  
                                                            this.setState({
                                                                    onelevelkey:i,//一级分类的下标
                                                                    onelevelid:commodity.categoryID,//一级分类id
                                                                    onelevelname:commodity.categoryName,//一级分类name
                                                                    twolevelid:'',
                                                                    twolevelname:'',
                                                                    changeclick:1
                                                            })

                                                        }}> {commodity.categoryName} </div> } >
                                            <div className="choose-person-right">
                                                <ul className="choose-person-content">
                                                    <Scrollbars>  
                                                         {commodity.children.map((child, index) => { 
                                                                return <li key={child.categoryID} 
                                                                            className={classnames({
                                                                                'choose-name-btn': true, 
                                                                                'selected':selloutStore.saveshop == child.categoryID  ? true : false
                                                                            })} 
                                                                            onClick={()=>{  
                                                                                 selloutStore.saveshop=child.categoryID; 
                                                                                this.setState({
                                                                                  twolevelid:child.categoryID,
                                                                                  twolevelname:child.categoryName,
                                                                                  onelevelid:'',
                                                                                  onelevelname:'',
                                                                                  changeclick:1
                                                                                }) 
                                                                            
                                                                            }}>
                                                                            {child.categoryName}
                                                                      </li>
                                                            })} 
                                                    </Scrollbars>
                                                </ul>
                                            </div>
                                    </TabPane>
                        });
                    }
                    })()}   
				</Tabs>
			</Modal>
		)

	}

}


export default CommodityIficationPopup;


