import React from 'react';
import {message} from 'antd';
import { Link } from 'react-router'; 
import { observer, inject } from 'mobx-react';  
import { browserHistory } from 'react-router'; //路径跳转 
import MyScroll from 'react-custom-scrollbars';//滚动条 
import CalendarChoose from '../calendar-shoose';//日历
import CommodityIficationPopup from '../commodity-ification-popup';//商品分类
import ZjEstimatePopup from '../zj-estimate-popup';//增加/减少/报损弹出层
import GqEstimatePopup from '../estimate-gq-popup';//沽清/删除弹出层
import ImportPopup from '../import-popup';//导入弹出层
import RightMemu from '../right-memu';//点菜组件
import Prompt from 'components/prompt-common';//错误提示
import './estimate-clear-total.less'; 
import '../../modal.css'; 

message.config({
	top: 300 
});

 
// 沽清列表组件
function SelloutLb({ selloutStore, sellout,handleClick}) { 
	 return <li  onClick={handleClick} className={sellout.selected === true ? 'each-data click-data' : 'each-data'} >
                <span>{sellout.index + 1}</span>
                <span>{sellout.productCode}</span>
                <span>{sellout.productName}</span>
                <span>{sellout.firstCategoryName}</span>
                <span>{sellout.secondCategoryName }</span>
                <span>{sellout.unitName}</span>
                <span>{sellout.optionName}</span>
                <span>{sellout.needWeigh ? sellout.preOrderFloatQuantity : sellout.preOrderQuantity }</span>
                <span>{sellout.needWeigh ? sellout.initFloatQuantity : sellout.initQuantity}</span> 
                <span>{sellout.needWeigh ? sellout.lossFloatQuantity : sellout.lossQuantity }</span>
                <span>{sellout.needWeigh ? sellout.remainingFloatQuantity : sellout.remainingQuantity }</span>
                 <span>{sellout.needWeigh ? sellout.orderedFloatQuantity : sellout.orderedQuantity }</span>
                <span>{(sellout.needWeigh ? sellout.remainingFloatQuantity : sellout.remainingQuantity) > 0 ? "在售": "售完"}</span>
                
            </li> 
}
//沽清
@inject('selloutStore') @observer
class EstimateClearTotal extends React.Component {
 

 constructor(props, context) {
		super(props, context);   
        this.state={
            Commodity:'',//商品分类弹出层
            ZjEstimatePopup:'',//增加/减少/报损弹出层 
            GqEstimate:'',//沽清/删除弹出层
            Menu:'',//右侧点菜组件
            isnodl:'false',//控制什么时候要出现导入的按钮
            Import:'',//导入弹出层
            addmune: '', //控制添加商品按钮的遮罩层
            recordValue:'',//查询的文本框值
            defaultvalue:'全部'
        };   
        let selloutStore=this.props.selloutStore;   
        selloutStore.getselloutList({storeDay:selloutStore.currentdate});//执行沽清列表action  0代表全部分类
        
	} 


    componentDidUpdate() {
        let selloutStore=this.props.selloutStore;  
        let feedback = selloutStore.feedback; 
        if(feedback) {
        //提示
            switch(feedback.status) {
                case 'success': 
                    message.success(feedback.msg,selloutStore.closeFeedback());
                    break;
                case 'warn':
                    message.warn(feedback.msg,selloutStore.closeFeedback());
                    break;
                case 'error':
                    message.warn(feedback.msg,selloutStore.closeFeedback());
                    break;
                default:
                    message.info(feedback.msg,selloutStore.closeFeedback());
            }
        }

         
    }


    //数据选中点击事件
    clicksellout(sellout) {   
        this.props.selloutStore.getafterobj(sellout);//点击的时候就获取【增加/减少/报损/沽清/删除】弹出层需要显示的字段以及确定按钮action需要的参数
        this.props.selloutStore.checkedselloutList(sellout.index);//判断是否被选中  
    }

    //获取搜索的内容
    reachValue=(e)=>{
        var value = e.target.value;
        this.setState({recordValue:value});

    }

    //操作历史
    operatehistory=(e)=>{
        let selloutStore=this.props.selloutStore;  
        browserHistory.push('/sellout/history'); //跳转到历史页面
        selloutStore.savelastcombination('0','','0');//清空商品分类选中组合
        selloutStore.saveshop='0';//恢复全部选项被选中
    }

  render() { 
    let selloutStore=this.props.selloutStore;  
    let feedback = selloutStore.feedback;
    let operatePrompt; 
    if(feedback && feedback.status === 'error') {
      //错误提示
      feedback.okClick = () =>{
        if(feedback.confirmClick) feedback.confirmClick();
        feedback.cancelClick();
      }
      feedback.cancelClick = () =>{
        selloutStore.closeFeedback();
      }
      operatePrompt = <Prompt message={feedback} />
    }

    let typename= '全部'  
    if(selloutStore.onelevel.onelevelname !== ""){
        typename=selloutStore.onelevel.onelevelname;
    }
    if(selloutStore.commoditytype.classifyName !== ""){
        typename=selloutStore.commoditytype.classifyName; 
    }
    


    return (
            <div className="estimate-claer-history-modal">
                <div className="estimate-main">
                    <div className="estimate-main-search">
                        <CalendarChoose 
                            estimeok={(e)=>{ //判断什么时候出现导入的按钮
                                this.setState({isnodl:e});
                            }}
                             getResult={(e)=>{//完成按钮
                                selloutStore.getselloutList({
                                    storeDay:e
                                }); 

                            }}
                            activeclick={(e)=>{//上一个日期下一个日期触发的查询
                                selloutStore.getselloutList({
                                    storeDay:e
                                });
                                
                                if(selloutStore.currentdate >e){
                                    selloutStore.getTakeeffect(); 
                                    if(selloutStore.Takeeffect === true){
                                        this.setState({isnodl:'false'});
                                    }else{
                                        this.setState({isnodl:'true'});
                                    }
                                }
                                
                            }}
                            >
                        </CalendarChoose>
                        <div className="search-right">
                            <div className="classification" 
                                onClick={()=>{ 
                                    selloutStore.getcommodityclassify();  
                                    this.setState({Commodity:
                                                   <CommodityIficationPopup 
                                                        closebutton={()=>{
                                                            this.setState({Commodity:''});
                                                        }}
                                                        okbutton={()=>{
                                                            this.setState({Commodity:'',
                                                                            defaultvalue:  selloutStore.lastcombination.lastname === ""  ? 
                                                                                '全部':
                                                                                selloutStore.lastcombination.lastname
                                                                });
                                                            selloutStore.getselloutList({
                                                                storeDay:selloutStore.currentdate,
                                                                categoryID: selloutStore.lastcombination.lastid === "" 
                                                                            ? '0'
                                                                            : selloutStore.lastcombination.lastid,
                                                            });
                                                        }}>
                                                    </CommodityIficationPopup>
                                                });
                                }}>
                                 {this.state.defaultvalue}
                                <i className="iconfont icon-Shapearrow-"></i>
                            </div>
                            <input type="text" placeholder="请输入商品编码/名称/拼音码查询" className="estimate-main-input"  onKeyUp={this.reachValue}></input>
                            <button className="search-box" 
                                    onClick={()=>{    
                                    selloutStore.getselloutList({
                                        storeDay:selloutStore.datetime,
                                        categoryID:selloutStore.lastcombination.lastid === "" 
                                                    ? '0'
                                                    : selloutStore.lastcombination.lastid,
                                        searchContent:this.state.recordValue
                                    });

                                }}> 
                                <i className="iconfont icon-order_btn_search" ></i>查询
                            </button>
                            {this.state.Commodity}
                        </div>
                    </div> 
                    <div className="main-block-history">
                        <div className="main-block">
                            <div className="estimate-list-button">
                                <div className="estimate-list-top"> 
                                            <span>序号</span>
                                            <span>商品编码</span>
                                            <span>商品名称</span>
                                            <span>一级分类</span>
                                            <span>二级分类</span>
                                            <span>单位</span>
                                            <span>规格</span>
                                            <span>本日预定</span>
                                            <span>沽清数量</span> 
                                            <span>报损量</span>
                                            <span>剩余数量</span>
                                            <span>已点数量</span>
                                            <span>状态</span>
                                </div>
                                <div className="estimate-list"> 
                                        <div className="estimate-scroll">
                                            <MyScroll >  
                                                  {selloutStore.selloutList.map((sellout,index) => { 
                                                        return <SelloutLb   key={index} 
                                                                            selloutStore={selloutStore} 
                                                                            sellout={sellout}  
                                                                            handleClick={this.clicksellout.bind(this, sellout)}
                                                                />
                                                    })}  
                                            </MyScroll>
                                        </div>
                                </div> 
                                {this.state.isnodl === 'false'    &&//日期是当前日期就显示【增加/减少/报损/沽清/删除/添加/操作历史】按钮
                                    <div className="normal-button">
                                        <button 
                                            onClick={()=>{  
                                                let flagt;
                                                selloutStore.selloutList.map((sellout,index)=>{
                                                    if(sellout.selected === true){
                                                        flagt=true;
                                                    }
                                                    return sellout;
                                                });

                                                if(flagt !== true){
                                                    message.warn("请选中数据");     
                                                }else{
                                                    this.setState({ZjEstimatePopup: 
                                                                    <ZjEstimatePopup 
                                                                            updateestime={()=>{
                                                                                this.setState({ZjEstimatePopup:''})
                                                                            }} 
                                                                            okbutton={()=>{    
                                                                                this.setState({ZjEstimatePopup:''}); 
                                                                            }}>
                                                                    </ZjEstimatePopup>
                                                                  }); 
                                                    selloutStore.getaddorreduce("1");//添加状态 
                                                    selloutStore.getreasonlist(selloutStore.addorreduce);//原因列表
                                                    this.props.selloutStore.reasontext.reasonName ="";//清空原因文本
                                                    this.props.selloutStore.issuccess ='';//清空返回状态

                                                }
                                            
                                            }}>增加沽清数量
                                        </button>
                                        <button  
                                            onClick={()=>{ 
                                                    let flagt;
                                                    selloutStore.selloutList.map((sellout,index)=>{
                                                        if(sellout.selected === true){
                                                            flagt=true;
                                                        }
                                                        return sellout;
                                                    });
                                                    if(flagt !== true){
                                                        message.warn("请选中数据");     
                                                    }else{
                                                        this.setState({ZjEstimatePopup:
                                                                       <ZjEstimatePopup 
                                                                            updateestime={()=>{
                                                                                this.setState({ZjEstimatePopup:''})
                                                                            }} 
                                                                            okbutton={()=>{   
                                                                                this.setState({ZjEstimatePopup:''}); 
                                                                            }}>
                                                                        </ZjEstimatePopup>
                                                                      });

                                                        selloutStore.getaddorreduce("2");//减少状态
                                                        selloutStore.getreasonlist(selloutStore.addorreduce);//原因列表
                                                        this.props.selloutStore.reasontext.reasonName ="";//清空原因文本
                                                        this.props.selloutStore.issuccess ='';//清空返回状态
                                                    } 
                                                }}>减少沽清数量
                                        
                                        </button>
                                        <button 
                                            onClick={()=>{ 
                                                let flagt;
                                                selloutStore.selloutList.map((sellout,index)=>{
                                                    if(sellout.selected === true){
                                                        flagt=true;
                                                    }
                                                    return sellout;
                                                }); 
                                                if(flagt !== true){
                                                    message.warn("请选中数据");     
                                                }else{
                                                    this.setState({ZjEstimatePopup:
                                                                  <ZjEstimatePopup 
                                                                        updateestime={()=>{
                                                                            this.setState({ZjEstimatePopup:''})
                                                                        }}  
                                                                        okbutton={()=>{ 
                                                                            this.setState({ZjEstimatePopup:''}); 
                                                                        }}>
                                                                  </ZjEstimatePopup>
                                                                  })
                                                    selloutStore.getaddorreduce("3");//报损状态
                                                    selloutStore.getreasonlist(selloutStore.addorreduce);//原因列表
                                                    this.props.selloutStore.reasontext.reasonName ="";//清空原因文本
                                                    this.props.selloutStore.issuccess ='';//清空返回状态
                                                }
                                            }}>制作报损
                                        </button>
                                        <button 
                                            onClick={()=>{
                                                let flagt;
                                                selloutStore.selloutList.map((sellout,index)=>{
                                                    if(sellout.selected === true){
                                                        flagt=true;
                                                    }
                                                    return sellout;
                                                });


                                                if(flagt !== true){
                                                        message.warn("请选中数据");     
                                                }else{
                                                        this.setState({GqEstimate:
                                                                       <GqEstimatePopup 
                                                                            gqclose={()=>{
                                                                                this.setState({GqEstimate:''});
                                                                            }} 
                                                                            okbutton={()=>{ 
                                                                                this.setState({GqEstimate:''}); 
                                                                            }}>
                                                                      </GqEstimatePopup>
                                                                      })
                                                        selloutStore.getaddorreduce("4");//沽清
                                                        selloutStore.getreasonlist(selloutStore.addorreduce);//原因列表
                                                        this.props.selloutStore.reasontext.reasonName ="";//清空原因文本
                                                        this.props.selloutStore.issuccess ='';//清空返回状态 
                                                }
                                            }}>沽清
                                        </button>
                                        
                                        <button 
                                            onClick={()=>{
                                                this.setState({
                                                    addmune:
                                                    <div>
                                                        <div className="mask" 
                                                            onClick={()=>{
                                                            this.setState({addmune:''});
                                                            }}>  
                                                        </div> 
                                                        <div className="mask-conten">
                                                            <RightMemu whatpopup={()=>{
                                                                this.setState({addmune:''});
                                                            }}></RightMemu>
                                                        </div> 
                                                    </div>
                                                    })
                                            }}>添加商品
                                        </button> 
                                        <button onClick={this.operatehistory}>操作历史</button> 
                                    </div>
                                }

                                {this.state.isnodl === 'true' && //日期小于当前日期显示【导入历史按钮】
                                <div className="history_botton">
                                    <button  
                                        onClick={()=>{ 
                                            this.setState({Import:
                                                            <ImportPopup 
                                                                importclose={()=>{
                                                                    this.setState({Import:''});
                                                                }} 
                                                                importok={()=>{ 
                                                                    selloutStore.implementImport(selloutStore.datetime,selloutStore.Importtype);//执行导入action
                                                                    this.setState({Import:''});
                                                                }}>
                                                            </ImportPopup>
                                                        });
                                        }}>导入历史
                                    </button>
                                </div>
                                } 

                                {this.state.ZjEstimatePopup} 
                                {this.state.GqEstimate}
                                {this.state.Menu}
                                {this.state.Import}
                                {this.state.addmune}
                                
                            </div>
                        </div>
                    </div>
                </div>
                {operatePrompt}
            </div>

            
 
    );
  }
}

EstimateClearTotal.wrappedComponent.contextTypes = {
	router: React.PropTypes.object.isRequired
}



export default EstimateClearTotal;



