import React from 'react';
import { Tabs, message } from 'antd';
import { inject, observer } from 'mobx-react'; 
import { browserHistory } from 'react-router';
import MyScroll from 'react-custom-scrollbars';//横向滚动条 
import UpdateEstimatePopup from '../update-estimate-popup';//添加沽清数量
import './right-memu.less';  
const TabPane = Tabs.TabPane;

message.config({
	top: 300 
}); 

// 菜品列表组件
function Dishes({ selloutStore, dishes, handleClick }) { 
	return <li onClick={handleClick}>
            <div className="dishes-info" >{dishes.productName} {dishes.productType === 1 && <div className="taocan">套餐</div>} </div>
            <div className="dishes-price">¥{dishes.price} /份</div> 
        </li>  
}


@inject('selloutStore') @observer
class RightMemu extends React.Component{

    constructor(props, context) {
		super(props, context);  
        this.state={UpdateEstimate:'',recordValue:''}; 
        
	}

     componentDidMount() { 
        let selloutStore = this.props.selloutStore;  
      	selloutStore.getFirstCategoryList(); //查询商品分类  
        selloutStore.getTakeeffect();//判断是否生效   
    }

  

  
    
    //menuID：菜单ID  
    clickDishes(dishes) {   
        this.setState({UpdateEstimate:
                      <UpdateEstimatePopup 
                            updateestime={()=>{
                                this.setState({UpdateEstimate:''});
                                this.props.selloutStore.emptylist();
                            }} 
                            okdateestime={()=>{ 
                                if(this.props.whatpopup){
                                    this.props.whatpopup();
                                }
                                this.setState({UpdateEstimate:''});
                                this.props.selloutStore.emptylist();
                            }}>
                      </UpdateEstimatePopup>}); 
        let selloutStore = this.props.selloutStore;  
        if(selloutStore.Takeeffect === true){ 
            browserHistory.push('/sellout/after'); //生效后
            //menuID：菜单ID(必填)
            this.props.selloutStore.getselloutMessage(dishes.menuID);
             this.props.selloutStore.savemenuID=dishes.menuID;
        }else if(selloutStore.Takeeffect === false){
             //menuID：菜单ID(必填) 
            this.props.selloutStore.getselloutMessage(dishes.menuID);  
             this.props.selloutStore.savemenuID=dishes.menuID; 
        } 
 
        this.props.selloutStore.menuname= dishes.productName; 
        this.props.selloutStore.optionIDSum= dishes.optionIDSum > 0 ? 1: 0;  
         
    }

    //获取搜索的内容
    reachValue=(e)=>{
            var value = e.target.value;
            this.setState({recordValue:value});
    }
  
    render(){ 
        let selloutStore = this.props.selloutStore; 
        return (
            <div id="right-memu">
                <div className="dishes-category">
                    <Tabs   activeKey={selloutStore.firstCategoryID+""} 
                            onTabClick={(categoryID) => {  
                                if(selloutStore.Takeeffect === true){  
                                    browserHistory.push('/sellout/after'); //生效后
                                    selloutStore.getSecondCategoryList(categoryID);
                                }else if(selloutStore.Takeeffect === false){ 
                                    selloutStore.getSecondCategoryList(categoryID);  
                                }   
                            }}>
                            {selloutStore.firstCategoryList.map((category) => {
                                return <TabPane tab={category.categoryName} key={category.categoryID} onClick={this.firstmemu}></TabPane>
                            })}
                    </Tabs>
                </div>

                <div className="dishes-nav">
                    <div className='nav-tabs'>
                        <Tabs   activeKey={selloutStore.secondCategoryID+""} 
                                onTabClick={(categoryID) => { 
                                    if(selloutStore.Takeeffect === true){ 
                                        browserHistory.push('/sellout/after'); //生效后
                                        selloutStore.getDishesList({categoryID}); 
                                    }else if(selloutStore.Takeeffect === false){
                                        selloutStore.getDishesList({categoryID}); 
                                    } 
                            
                                }}>
                                {selloutStore.secondCategoryList.map((category) => {
                                    return <TabPane tab={category.categoryName} key={category.categoryID} onClick={this.secondmemu}></TabPane>
                                })}
                        </Tabs>
                    </div>
                    <div className='nav-search'>
                        <input  type="text" placeholder="搜索菜品"  onKeyUp={this.reachValue}
                                onChange={(e) =>{ 
                                    if(selloutStore.Takeeffect === true){ 
                                        browserHistory.push('/sellout/after'); //生效后   
                                        selloutStore.getDishesList({categoryID:selloutStore.secondCategoryID,searchCode: e.target.value});
                                    }else if(selloutStore.Takeeffect === false){ 
                                        selloutStore.getDishesList({categoryID:selloutStore.secondCategoryID,searchCode: e.target.value});
                                    } 
										
						        }} />
                        <i className="iconfont icon-order_btn_search"></i>
                    </div>
                </div>
            
               <ul className="dishes-list"> 
                    <MyScroll>
                        {selloutStore.dishesList.map((dishes,index) => {    
                           return <Dishes key={index} selloutStore={selloutStore} dishes={dishes} handleClick={this.clickDishes.bind(this, dishes)}  />
                        })} 
                    </MyScroll>
                   
                </ul>
               <div className="dishes-pagination">
                    <div>上一页</div>
                    <div>下一页</div>
                </div>
                 {this.state.UpdateEstimate}
            </div>
           
        );
    }
}

export default RightMemu;



